import React from 'react'
import Icon from 'components/Icon-svg'
import CircleLoading from 'components/CircleLoading'
import connect from 'connect'
import './index.less'
import { Button, WhiteSpace, WingBlank, Carousel, List, Grid, Card, Tabs, NavBar, Flex } from 'antd-mobile';
@connect
export default class extends React.Component {
    state = {
        loading: false
    }
    async componentWillMount () {
        
    }
    // 点赞
    likeNum (info) {
        info.islike = !info.islike
        if (info.islike) {
            info.like_num ++
        }else{
            info.like_num --
        } 
        this.props.renderHeadlineList()
    }
    //属性置反
    attrReverse (info, attr) {
        info[attr] = !info[attr]
        this.props.renderArticle()
    }
    render () {
        const {articleInfo} = this.props.state.common
        const data = [
          {
            img: require('../../images/lian.png'),
            title: 'Meet hotel',
            des: '新闻标题新闻标题新闻标题',
            value: 0,
          },
          {
            img: require('../../images/lian.png'),
            title: 'McDonald\'s invites you',
            des: '新闻标题新闻标题新闻标题',
            value: 1,
          },
          {
            img: require('../../images/lian.png'),
            title: 'Eat the week',
            des: '新闻标题新闻标题新闻标题',
            value: 2,
          },
        ];
        return (
            <article className="article-wrapper">
            <NavBar
                 mode="light"
                 icon={<img src={require('../../images/back.png')} alt="LOGO" className="back"  onClick={this.props.history.goBack}/>}
               >详情</NavBar>
                <div className="articel">
                    <h2>新闻标题新闻标题新闻标题新闻标题新闻标题新闻标题新闻标题新闻标题</h2>
                   <div className="info-a">
                           <span>2018-07-11</span>
                           <span>浏览：101次</span>
                   </div>
                    <p className="intro">新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容</p>
                    <img src={require('../../images/lian.png')} alt="LOGO" className="lian"  onClick={this.props.history.goBack}/>
                    <p className="intro">新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容新闻内容</p>
                    <Flex justify="end">
                        <img src={require('../../images/tt_sc2.png')} alt="LOGO" className="sc inline" />
                        <img src={require('../../images/tt_dz.png')} alt="LOGO" className="dz inline" />
                    </Flex>
                    <div className="floetop">上一篇：标题标题标题标题标题标题标题标题标题.</div>
                    <div className="floetop">下一篇：标题标题标题标题标题标题标题标题标题.</div>
                </div>
                <WhiteSpace/>
                <NavBar
                     mode="dark"
                     leftContent="为您推荐"
                   ></NavBar>
                <div style={{ flex:1 , backgroundColor: '#efefef', margin: '0 auto' }}>
                            {data.map((obj, index) => (
                        <div  key={'myordercard' + index} >
                         <Card full={true}>
                               <Card.Header
                                 thumb={obj.img}
                                 thumbStyle={{width: '1rem',height: '0.75rem', borderRadius: '0.03rem'}}
                                 extra={
                                  <div style={{ }}>
                                    <div className="destext">{obj.des}</div>
                                    <div><span className="jiatext">2017-02-25. 浏览：10102次</span></div>
                                    <div className="destext"></div>
                                  </div>
                                 }
                               />
                         </Card>
                      </div>
                       ))}
                </div>
                
            </article>
        )
    }
}



